import React from 'react'
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import './store';
import {connect} from 'react-redux';

class Jump extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
            str:'iconfont icon-shoucang'
        };
    }
    getlist=()=>{
        const L=this.props.sc;
        const I=this.props.index;
        const O=this.props.datalist[I];
        if(L.length<1){
            this.props.dispatch({
                type:'SC',
                sc:O

            })
            this.setState({
                str:'iconfont icon-shoucang-copy'
            })
        }else{
            for(var i=0;i<L.length;i++){
                if(O.id!==L.id){
                    this.props.dispatch({
                            type:'SC',
                            sc:O
                    })
                    //
                    this.setState({
                        str:'iconfont icon-shoucang-copy'
                    })
                    break;
                }
            }
        }

   }

    render(props){
    return (
        <div>
            <span onClick={()=>{
                this.props.history.goBack('/Home')
            }}>{`<=返回`}</span>
            {/* <img src={this.props.location.shuzu[index].img}/> */}
            <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white">
                    <TabBar.Item
                        title="店铺"
                        key="dianpu"
                        icon={
                            <i className='iconfont icondianpu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont icondianpu'></i>                           
                        }
                        onPress={ () => {
                            props.history.push('/Dianpu');
                        } }
                        data-seed="logId">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont iconkefu'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconkefu'></i>                           
                        }
                        title="客服"
                        key="cart"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }
                        data-seed="logId1">
                    </TabBar.Item>
                    <TabBar.Item

                        icon={
                            <i className={'iconfont iconshoucang'}></i>
                        }
                        selectedIcon={
                            <i className='iconfont iconshoucang-'></i>
                        }
                        title="收藏"
                        key="order"
                        onPress={ () => {
                            this.setState({
                                selectedTab:"yellowTab"
                            })
                            if(localStorage.getItem("username")!=null && localStorage.getItem("password")!=null){
                                this.props.history.push('/cart')
                            }else{
                                this.props.history.push('/Login')
                            }
                        } }
                        data-seed="logId2">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont iconusercenter'></i> }
                        selectedIcon={<i className='iconfont iconusercenter'></i> }
                        title="加入购物车"
                        key="taobao"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }>      
			        </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont iconusercenter'></i> }
                        selectedIcon={<i className='iconfont iconusercenter'></i> }
                        title="立即购买"
                        key="taobao"
                        onPress={ () => {
                            props.history.push('/dianpu')
                        } }
                    >      
			        </TabBar.Item>
                </TabBar>
        </div>
    )
}}

    const tiaozhuan = (state)=>{
        return {
        sc:state.sc,
            userinfo:state.login,
            index:state.change,
            datalist:state.todo
        }
    }

export default connect(tiaozhuan)(withRouter(Jump));
