class FlexExample extends React.Component {
    constructor() {
        super();
    }
    state = {
        photo: [],

    }
    
    componentDidMount = () => {
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then((res) => res.json())
            .then((myJson) => {

                let values = myJson.data;

                this.props.dispatch({
                    type:'ADD_ITEM',
                    value:values
                })

                this.props.dispatch({
                    type:'AD_TODO',
                    value:values
                })

                
                this.setState({
                    photo: values
                })
            });
         
    }
     
    takephoto(things) {
         let shuzu = this.state.photo;
        for (var i = things; i < shuzu.length; i++) {
            return (
                 <Flex>
                    <Flex.Item><img style={{ width: '198px', height: '198px', }}
                        onClick={() => {  
                            this.props.dispatch({
                                type:'',
                                index:i
                            })
                            this.props.history.push("/Jump")}}
                        src={shuzu[i].img} ></img>
                            <p>{shuzu[i].price}</p>
                            <p>{shuzu[i].title}</p>
                    </Flex.Item>
                    <Flex.Item><img style={{ width: '200px', height: '200px' }}
                        onClick={() => {
                            this.props.dispatch({
                                type:'',
                                 index:i+1
                            })
                            this.props.history.push("/Jump")}}
                        src={shuzu[i + 1].img}></img>
                            <p>{shuzu[i + 1].price}</p>
                            <p>{shuzu[i + 1].title}</p></Flex.Item>
                 </Flex>
            )
        }
}

    render() {
        return (
            <div>
                <div className="tupian">
                    <div className="shuzi">
                        {this.takephoto(0)}
                        {this.takephoto(1)}
                        {this.takephoto(2)}
                        {this.takephoto(3)}                       
                    </div>
                </div>
            </div>
        )
    }

}

const mapStateToProps = (state)=>{
    return {
        mydata:state.todo,
        change:state.change,
        userinfo:state.login
    }
}

export default connect(mapStateToProps)(withRouter(FlexExample));