import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';

class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white">
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont iconshouye'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconshouye'></i>                           
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont iconshoucang'></i>                           
                        }
                        selectedIcon={
                            <i className='iconfont iconshoucang'></i>                           
                        }
                        title="收藏"
                        key="cart"
                        selected={ this.state.selectedTab=='yellowTab' }
                        onPress={ () => {
                            this.setState({
                                selectedTab:"yellowTab"
                            })
                            if(localStorage.getItem("username")!=null && localStorage.getItem("password")!=null){
                                this.props.history.push('/cart')
                            }else{
                                this.props.history.push('/Login')
                            }
                        } }
                        data-seed="logId1">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icondingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icondingdan'></i>
                        }
                        title="订单列表"
                        key="order"
                        selected={ this.state.selectedTab === 'yellowTab' }
                        selected={ pathname === 'order' }
                        onPress={ () => {
                            this.props.history.push('/order')
                        } }
                        data-seed="logId2">
                    </TabBar.Item>
                    <TabBar.Item
                        icon={<i className='iconfont iconwode'></i> }
                        selectedIcon={<i className='iconfont iconwode'></i> }
                        title="我的淘宝"
                        key="taobao"
                        selected={ this.state.selectedTab === 'yellowTab' }
                        onPress={ () => {
                            this.props.history.push('/taobao')
                        } }>      
			        </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);


