import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import Jump from './taobao/Jump';
import Order from './taobao/Order';
import Dianpu from './taobao/Dianpu';
import Lookfor from './taobao/Lookfor';
import Taobao from './taobao/Taobao';
import Login from './taobao/Login';
import { Provider } from 'react-redux';
import store from'./taobao/store';


const App = ()=>{
	return<Provider store={store}>
		<Router>
			<Switch>
				<Route exact path='/' component={Home} />
				<Route path='/cart' component={Cart} />
				<Route path='/Jump' component={Jump} />
				<Route path='/Login' component={Login} /> 
				<Route path='/home/detail' component={Cart} />
				<Route path='/order' component={Order} />
				<Route path='/home/detail' component={Order} />
				<Route path='/dianpu' component={Dianpu} />
				<Route path='/home/detail' component={Dianpu} />
				<Route path='/taobao' component={Taobao} />
				<Route path='/home/taobao' component={Taobao} />
				<Route path='/home/lookfor' component={Lookfor}></Route>
				<Route render={()=><Redirect to='/'/>} />
			</Switch>
		
		</Router>
	</Provider>

}
export default App;

