import React from 'react';
import { Carousel, WingBlank } from 'antd-mobile';

class Lunbo extends React.Component {
  state = {
    data: ['1', '2', '3'],
    imgHeight: 176,
  }
  componentDidMount() {
    setTimeout(() => {
      this.setState({
        data: ['AiyWuByWklrrUDlFignR', 'TekJlZRVCjLFexlOCuWn', 'IJOtIlfsYdTyaDTRVrLI'],
      });
    }, 100);
  }
  render() {
    return (
      <WingBlank>
        <Carousel
          autoplay={true}
          infinite
          beforeChange={(from, to) => console.log(`slide from ${from} to ${to}`)}
          afterChange={index => console.log('slide to', index)}>
            <img src='https://aecpm.alicdn.com/imgextra/i3/734584252/O1CN01kjXfBN1hHShXKRpPy_!!734584252-0-alimamazszw.jpg'/>
            <img src ="https://aecpm.alicdn.com/imgextra/i3/734584252/O1CN0104HF6i1hHShYlo8vd_!!734584252-0-alimamazszw.jpg"/>
            <img src ='https://aecpm.alicdn.com/imgextra/i2/676606897/O1CN01BPK61b20osISAVGSN_!!676606897-0-alimamazszw.jpg'/>
        </Carousel>
      </WingBlank>
    );
  }
}

export default Lunbo